import React from 'react'
import { NewEventsWrapper, NewEventsItem, NewEventsItemImage, NewEventsItemName, NewEventsButton, PlusIcon } from './NewEvents.style'
import { Row, Column } from 'styles'
import { history } from 'helpers'

class NewEvents extends React.Component {

    handleMoreEvent = () => {
        history.push('/eventos');
    }

    render() {
        const { events } = this.props;

        return (
            <NewEventsWrapper>
                <Row bottom="10px">
                    <Column>
                        <NewEventsItem>
                            <NewEventsItemImage imageSrc="images/event1.jpg" alt="" />
                            <NewEventsItemName>Events</NewEventsItemName>
                        </NewEventsItem>
                    </Column>
                    <Column>
                        <NewEventsItem>
                            <NewEventsItemImage imageSrc="images/event2.jpg" alt="" />
                            <NewEventsItemName>Events</NewEventsItemName>
                        </NewEventsItem>
                    </Column>
                </Row>
                <Row>
                    <Column>
                        <NewEventsItem>
                            <NewEventsItemImage imageSrc="images/event3.jpg" alt="" />
                            <NewEventsItemName>Events</NewEventsItemName>
                        </NewEventsItem>
                    </Column>
                    <Column>
                        <NewEventsButton onClick={this.handleMoreEvent} aria-label="Botão para mostrar de eventos" ><PlusIcon size={50} /></NewEventsButton>
                    </Column>
                </Row>
            </NewEventsWrapper>
        );
    }
}

export { NewEvents }
