import React from 'react'
import ImageGallery from 'react-image-gallery'
import '../../../node_modules/react-image-gallery/styles/css/image-gallery-no-icon.css'
import { ImageWrapper } from './Carousel.style'

class Carousel extends React.Component {

    render() {
        const { events } = this.props;
        const images = events.length ? events.map(event => (
            {
                original: `${event.image ? `images/${event.image}` : 'images/event-image-default.png'}`,
                description: event.name,
                originalAlt: '',
            }
        )) : [];

        return (
            <ImageWrapper>
                {events.length ?
                    <ImageGallery items={images} autoPlay={true} showThumbnails={false} />
                    :
                    null}
            </ImageWrapper>
        );
    }
}

export { Carousel }
